*, *:after, *:before {
  box-sizing: border-box;
}

body, html {
    padding: 0;
    margin: 0;
    height: 100%;
    min-height: 100%;
    width: 100%;
    background-color: #BBB;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}

my-app {
  height: 100%;
  width: 100%;
}


/* Four quadrants basic setup */

.q4 > ng-component {
    width: 100%; 
    padding: 20px 80px 70px;
    overflow-y: scroll;
    flex-grow: 1;
}

.q4 > ng-component::-webkit-scrollbar { 
  width: 0 !important 
}


.q4 > ng-component .negative-margin {
    padding: 0;
    margin: 0 -50px !important;
}

.q4 > ng-component .container-no-padding {
    padding: 0 !important;    
}

.q4 ng-component .list-wrapper {
    padding-left: 27px;
}

.q4 ng-component ul{
    padding: 0;
    list-style: none;
}

.q4 ng-component ul li{
    padding: 0.3rem 0;
    font-size: 1.3rem;
    position: relative;
}

.q4 ng-component p{
    padding: 0.5rem 0;
    font-size: 1.3rem;
}

.q4 ng-component ul li:before {
    content:"â–¶";
    position: absolute;
    top: 0.5rem;
    left: -30px;
    color: #000;
}

.q4 ng-component ul.list-minified li{
    padding: 0.3rem 0;
}

.q4 ng-component ul.list-minified li:before{
    top: 0.3rem;
}

/* Zoom q4 */

.q4 {
  position: absolute !important;
  bottom: 0;
  right: 0;
}

.q4.zoomed {
  
  width: 100% !important;
  height: 100% !important;
}

/* global page styles */

a {
  color:#fff;
}

.absolute {
  position: absolute;
  top: 0;
  left: 0;
}

.relative {
  position: relative;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.block-centered-600 {
  display: block;
  margin: 0 auto;
  max-width: 600px;
}

.centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.full-img {
    max-width: 100%;
}
.example-img {
    max-height: 10rem;
    max-width: 100%;
}

.example-img-mid {
    max-height: 14rem;
    max-width: 100%;
}

.example-img-lg {
    max-width: 100%;
}

.example-img-xl {
    width: 100%;
}

.example-col-150 {
  height: 150px;
  display: flex;
}


.example-col-75 {
  height: 75px;
  display: flex;
  
}

.example-col-75 div, .example-col-150 div{
    background: white;
    margin-bottom: 20px;
    width: 100%;
    padding: 15px;
}

.support-paragraph {
  margin-top: 0 !important;
  padding-top: 0 !important;
}

.inline-links a {
  padding-right: 20px;
}

.compare-table{
  width: 100%;
}

.compare-table .mid-cell{
  width: 80px;
  text-align: center;
}

.compare-table tr td {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 2px solid #488e35;
}


/* colors page - button */

.button-wrap {
  padding: 20px 0;
}

.button-wrap > span{
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 1.2rem; 
  cursor: pointer;
}

.button-1 {
  background-color: #488e35;
  color:white;
}

.button-1:hover {
  background: #4eb332;
}

.button-wrap-1 {
  background: #2be26c;
  text-align: center;
  border-radius: 8px;
  margin-bottom: 10px;
}

.button-2 {
  background-color: #488e35;
  color:white;
}

.button-3 {
  background-color: white;
  color:#488e35;
}


.button-4 {
  background-color: #488e35;
}


@media (max-width:660px){
    .q4 > ng-component {
        padding: 30px 40px 20px;
    }

    .q4 > ng-component .negative-margin {
      margin: 0 0 !important;
    }

    .q4 ng-component ul{
        padding-left: 30px;
    }

